import React from "react";
import "./ImageContainer.css";
const ImageContainer = ({ width, picture, click, pins }) => {
  const widthe = () => {
    if (width > 1500) {
      return 1280;
    } else if (width <= 1500 && width > 1000) {
      return 853;
    } else if (width <= 1000 && width > 700) {
      return 640;
    } else if (width <= 700 && width > 450) {
      return 426;
    } else {
      return 336;
    }
  };
  return (
    <div className="image_container">
      <div onClick={click} style={width&& {width:`${widthe()}px`}} className="photo_container">
        <img className="photo" src={picture} alt="cysterna" />
        {pins && pins.map((pin) => pin)}
      </div>
    </div>
  );
};

export default ImageContainer;
