import React, { useState, useEffect, useRef } from "react";
import ReactPlayer from "react-player/file";
import "./VideoContainer.css";
const VideoContainer = ({
  video,
  width,
  pins,
  scroll,
  delay,
  control = false,
}) => {
  const [isVisible, makeVisible] = useState(false);
  const [loaded, setLoaded] = useState(false);
  const scrollView = useRef(null);
  const widthe = () => {
    if (width > 1500) {
      return 1280;
    } else if (width <= 1500 && width > 1000) {
      return 853;
    } else if (width <= 1000 && width > 700) {
      return 640;
    } else if (width <= 700 && width > 450) {
      return 426;
    } else {
      return 336;
    }
  };
  const heighte = () => {
    if (width > 1500) {
      return 720;
    } else if (width <= 1500 && width > 1000) {
      return 480;
    } else if (width <= 1000 && width > 700) {
      return 360;
    } else if (width <= 700 && width > 450) {
      return 240;
    } else {
      return 189;
    }
  };
  useEffect(() => {
    if (loaded) {
      setTimeout(() => {
        makeVisible(true);
      }, delay);
      if (scroll) {
        scrollView.current.scrollIntoView({ behavior: "smooth" });
      }
    }
  }, [loaded, scroll]);
  return (
    <div ref={scrollView} className="video_container">
      <div className="player_container">
        <ReactPlayer
          url={video}
          playing={true}
          controls={control}
          muted={true}
          playsinline={true}
          height={heighte()}
          width={widthe()}
          onBufferEnd={() => {
            setLoaded(true);
          }}
        />
        {!loaded && (
          <div className="loader">
            <div className="lds-ring">
              <div></div>
              <div></div>
              <div></div>
              <div></div>
            </div>
          </div>
        )}
        {isVisible && pins ? pins.map((pin) => pin) : null}
      </div>
    </div>
  );
};

export default VideoContainer;
