import React, { useState } from "react";
import VideoContainer from "../components/VideoContainer";
import Pin from "../components/Pin";
import Button from "../components/Button";
import jam1 from "../media/jam1.mp4";
import jam2 from "../media/jam2.mp4";
import jam3 from "../media/jam3.mp4";
import jam4 from "../media/jam4.mp4";

const Back = ({ pinsize, width, click }) => {
  const [videoVisible1, setVisible1] = useState(false);
  const [videoVisible2, setVisible2] = useState(false);
  const [videoVisible3, setVisible3] = useState(false);

  const showVideo1 = () => {
    setVisible1(!videoVisible1);
    setVisible2(false);
    setVisible3(false);
  };
  const showVideo2 = () => {
    setVisible2(!videoVisible2);
    setVisible1(false);
    setVisible3(false);
  };
  const showVideo3 = () => {
    setVisible3(!videoVisible3);
    setVisible1(false);
    setVisible2(false);
  };
  //Pins for main screen
  const pins_main = [
    <div onClick={showVideo1} key={1}>
      {" "}
      <Pin top={47} left={30} width={pinsize} height={pinsize} delay={0.1} />
    </div>,
    <div onClick={showVideo3} key={2}>
      {" "}
      <Pin top={8} left={35} width={pinsize} height={pinsize} delay={0.2} />
    </div>,
    <div onClick={showVideo2} key={3}>
      {" "}
      <Pin top={55} left={47} width={pinsize} height={pinsize} delay={0.3} />
    </div>,
  ];
  return (
    <>
      <VideoContainer video={jam1} width={width} pins={pins_main} delay={1500} />
      {videoVisible1 && (
        <VideoContainer video={jam2} width={width} scroll={videoVisible1} control={true} />
      )}
      {videoVisible2 && (
        <VideoContainer video={jam3} width={width} scroll={videoVisible2} control={true}/>
      )}
      {videoVisible3 && (
        <VideoContainer video={jam4} width={width} scroll={videoVisible3} control={true} />
      )}
      <center>
        <Button doThis={click}/>
      </center>
    </>
  );
};

export default Back;
