import React, { useState, useEffect} from "react";
import Headline from "./components/Headline";
import jamox from "./media/jamox.png";
import FrontPage from "./layouts/FrontPage";
import Back from "./layouts/Back";
import Left from "./layouts/Left";
function App() {
  const [width, setWidth] = useState(null);
  const [front, setFront] = useState(true);
  const [back, setBack] = useState(false);
  const [left, setLeft] = useState(false);
  const pinsize = width > 1500 ? 70 : width < 700 ? 30 : 50;

  const openBack = () => {
    setFront(false);
    setBack(true);
  }
  const closeBack = () => {
    setFront(true);
    setBack(false);
  }
  const openLeft = () => {
    setFront(false);
    setLeft(true);
  }
  const closeLeft = () => {
    setFront(true);
    setLeft(false);
  }

  useEffect(() => {
    window.addEventListener("load", () => {
      setWidth(parseInt(window.innerWidth.toFixed(0)));
    });
    window.addEventListener("resize", () => {
      setWidth(parseInt(window.innerWidth.toFixed(0)));
    });
  }, []);
  return (
    <>
      <Headline image={jamox} />
      {front && <FrontPage back={openBack} left={openLeft} width={width} pinsize={pinsize} />}
      {back && <Back click={closeBack} pinsize={pinsize} width={width} />}
      {left && <Left click={closeLeft} pinsize={pinsize} width={width}/>}
    </>
  );
}

export default App;
