import React, {useState} from "react";
import VideoContainer from "../components/VideoContainer";
import Button from "../components/Button";
import Pin from "../components/Pin";
import jam5 from "../media/jam5.mp4";
import jam6 from "../media/jam6.mp4";
import jam7 from "../media/jam7.mp4";
import jam8 from "../media/jam8.mp4";
const Left = ({ click, pinsize, width }) => {
    const [videoVisible1, setVisible1] = useState(false);
    const [videoVisible2, setVisible2] = useState(false);
    const [videoVisible3, setVisible3] = useState(false);
    const showVideo1 = () => {
        setVisible1(!videoVisible1);
        setVisible2(false);
        setVisible3(false);
      };
      const showVideo2 = () => {
        setVisible2(!videoVisible2);
        setVisible1(false);
        setVisible3(false);
      };
      const showVideo3 = () => {
        setVisible3(!videoVisible3);
        setVisible1(false);
        setVisible2(false);
      };
  const pins_main = [
    <div key={1} onClick={showVideo1}>
      <Pin top={14} left={92} width={pinsize} height={pinsize} delay={0.1} />
    </div>,
    <div key={2} onClick={showVideo2}>
      <Pin top={53} left={width> 1000 ? 94 : 92} width={pinsize} height={pinsize} delay={0.2} />
    </div>,
    <div key={3} onClick={showVideo3}>
      <Pin top={55} left={41} width={pinsize} height={pinsize} delay={0.3} />
    </div>,
  ];
  return (
    <>
       <VideoContainer video={jam6} width={width} scroll={videoVisible2} pins={pins_main} delay={2000} />
      {videoVisible1 && (
        <VideoContainer video={jam7} width={width} scroll={videoVisible1} control={true} />
      )}
      {videoVisible2 && (
        <VideoContainer video={jam5} width={width} scroll={videoVisible2} control={true} />
      )}
      {videoVisible3 && (
        <VideoContainer video={jam8} width={width} scroll={videoVisible3} control={true} />
      )}
      <center>
        <Button doThis={click} />
      </center>
    </>
  );
};

export default Left;
