import React from "react";
import ImageContainer from "../components/ImageContainer";
import jamox_left from "../media/jamox_left.jpg";
import jamox_right from "../media/jamox_right.jpg";
import jamox_back from "../media/jamox_back.jpg";
const FrontPage = ({ width, back,left }) => {
  return (
    <>
      <center>
        <h3 style={{ margin: "20px ", color: "gray" }}>
          Kliknij poniżej aby dowiedzieć się więcej
        </h3>
      </center>
      <ImageContainer click={left} width={width} picture={jamox_left} />
      <ImageContainer width={width} picture={jamox_right} />
      <ImageContainer click={back} width={width} picture={jamox_back} />
    </>
  );
};

export default FrontPage;
